#root {
  height: 100%;
}

.height-100 {
  height: 100%;
}

.App {
  border: 3px solid #fff;
  border-radius: 1px;
}

.grid {
  font-size: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}
.row {
  width: auto;
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 6px solid #96B7E7;
}
.cell {
  border-right: 6px solid #96B7E7;
  padding: 5px;
  width: 5rem;
  height: 5rem;
  line-height: 20px;
  font-weight: bold;
  font-size: 2em;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;

  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
  cursor: pointer;
  -webkit-transition: all 1s ease-in;
  -o-transition: all 1s ease-in;
  transition: all 1s ease-in;
}
.cell--set {
  color: #96B7E7;
}

.cell:first-child {
  border-top: 0;
  border-left: 0;
}

.cell:last-child {
  border-right: 0;
  border-bottom: 0;
}

.row:first-child {
  border-top: 0;
}

.row:last-child {
  border-bottom: 0;
}

.text-white {
  color: white;
}

.WinGame {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
  font-size: 4rem;
}



